/* imports */

/* header */

.title-img {
  margin-top: 20px;
  width: 400px;
}

.title-img {
  margin-top: 20px;
  width: 350px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.side-img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 250px;
  margin-top: 40px;
}

.header-nav {
  position: relative;
  width: 100%;
  height: auto;
  background-color: #ffff;
  text-align: center;
  overflow: hidden;
  text-transform: uppercase;
  font: 12px "opensans-bold", sans-serif;
  text-transform: uppercase;
  letter-spacing: 2.5px;
  margin: 0 auto;
  z-index: 100;
  position: fixed;
  left: 0;
  padding-top: 20px;
}

.header-nav a {
  text-decoration: none;
  display: inline-block;
  padding: 8px 13px;
  line-height: 32px;
  text-decoration: none;
  color: rgb(26, 26, 26);

  -webkit-transition: color 0.2s ease-in-out;
  transition: color 0.2s ease-in-out;
}

.header-nav ul {
  width: auto;
  text-align: center;
  display: inline-block;

  list-style: none;
}

.header-nav ul li {
  float: left;
}

.side-menu {
  display: none;
}

@media only screen and (max-width: 500px) {
  .header-nav {
    /* visibility: hidden; */
    display: none;
  }
  .side-menu {
    display: block;
    /* visibility: visible; */
  }
}

/* imports */

/* landing page */

.header-content {
  height: 100vh;
  overflow: hidden;
}

/* content grid */

.landing-grid {
  text-align: center;
  position: fixed;
  top: 15%;
  left: 0;
  height: 100%;
  width: 100%;
}

.avatar {
  height: 400px;
  width: 300px;
}

.welcome {
  display: block;
}

.header-logo {
  width: 90%;
  display: none;
}

.name-icon {
  width: 20%;
}

.banner-text {
  width: 50%;
  margin: auto;
  margin-top: 20px;
  border-radius: 4px;
  padding: 10px;
  font-size: 30px;
  border: 4px solid rgb(22, 22, 22);
  padding: 15px;
  margin-bottom: 20px;
}

.social-links {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 50%;
}

.social-links i {
  color: rgb(3, 3, 3);
  font-size: 1.5em;
  padding: 10px;
}

/* scroll button */

.scroll-down {
  opacity: 1;
  -webkit-transition: all 0.5s ease-in 3s;
  transition: all 0.5s ease-in 3s;
}

.scroll-down {
  position: absolute;
  bottom: 30px;
  left: 50%;
  margin-left: -16px;
  display: block;
  width: 32px;
  height: 32px;
  border: 2px solid rgb(20, 20, 20);
  background-size: 14px auto;
  border-radius: 50%;
  z-index: 2;
  -webkit-animation: bounce 2s infinite 2s;
  animation: bounce 2s infinite 2s;
  -webkit-transition: all 0.2s ease-in;
  transition: all 0.2s ease-in;
  -webkit-transform: scale(1);
          transform: scale(1);
}

.scroll-down:before {
  position: absolute;
  top: calc(50% - 8px);
  left: calc(50% - 6px);
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  display: block;
  width: 12px;
  height: 12px;
  content: "";
  border: 2px solid rgb(37, 37, 37);
  border-width: 0px 0 2px 2px;
}

@-webkit-keyframes bounce {
  0%,
  100%,
  20%,
  50%,
  80% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  40% {
    -webkit-transform: translateY(-10px);
    transform: translateY(-10px);
  }
  60% {
    -webkit-transform: translateY(-5px);
    transform: translateY(-5px);
  }
}

@keyframes bounce {
  0%,
  100%,
  20%,
  50%,
  80% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  40% {
    -webkit-transform: translateY(-10px);
    transform: translateY(-10px);
  }
  60% {
    -webkit-transform: translateY(-5px);
    transform: translateY(-5px);
  }
}

@-webkit-keyframes text {
  0% {
    color: rgb(255, 255, 255);
    margin-bottom: 40px;
  }
}

@keyframes text {
  0% {
    color: rgb(255, 255, 255);
    margin-bottom: 40px;
  }
}

/* media query for mobile layout */

@media only screen and (max-width: 1050px) {
  .banner-text {
    font-size: 25px;
  }
}

@media only screen and (max-width: 500px) {
  .top-menu {
    display: none;
  }
  .landing-grid {
    top: 10%;
  }

  .header-logo {
    display: block;
  }

  .welcome {
    display: none;
  }

  .banner-text {
    width: 80%;
    margin-top: 20px;
    font-size: 25px;
  }

  .social-links i {
    font-size: 1.5em;
    padding: 10px;
  }

  .avatar {
    height: 250px;
    width: 170px;
  }
}

@media only screen and (max-height: 700px) {
  .landing-grid {
    top: 20%;
  }
  
  .avatar {
    height: 300px;
    width: 200px;
  }

  .banner-text {
    font-size: 24px;
    width: 40%;
  }
}

